import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const state = {
  stepName: ['', '', '', '', '', ''],
  steps: [1, 2, 3, 4, 5, 6],
  step: 1,
  description: ['', '', '', '', '', ''],
  radioStep1: '',
  form: {
    company: 'aaa',
    name: 'bbb',
    state: [],
    sizeAndCapacitys: [],
    categorys: [],
    type: [],
    voltagelevel: '',
    pro: '',
    major: '',
    startTime: '',
    endTime: ''
  },
  qualificationRequirements: { CET: '' },
  projectCharacteristics: { difficulty: '', price: '' },
  existingData: { detail: '' },
  states: ['', '', ''],
  sizeAndCapacitys: ['MW', 'KW', 'kVA'],
  categorys: ['', '', '', '', '', ''],
  type: ['', '', '', '', '']
}

const mutations = {
  init(state) {
    state.step = 1
  },
  init2(state) {
    state.step = 2
  },
  init3(state) {
    state.step = 3
  },
  init4(state) {
    state.step = 4
  },
  init5(state) {
    state.step = 5
  },
  init6(state) {
    state.step = 6
  }
}
export default new Vuex.Store({
  state,
  mutations
})



